/*
*
* Pricing
*/
@keyframes object-hide {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0.1;
  }
  80%,
  100% {
    opacity: 0;
  }
}

@keyframes object-show {
  0% {
    opacity: 0;
    transform: scale3d(0.9, 0.9, 0.9);
  }
  33% {
    opacity: 0.05;
  }
  75% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}

@keyframes opacity-loop {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

