  background-color: #ffffff;
}

#loading-center-object {
  width: 200px;
  height: 200px;
  transform: rotate(-135deg);
}

.preloader-body {
  text-align: center;
}

.object {
  border-radius: 50%;
  position: absolute;
  border: 4px solid #0accbe;
  border-bottom-color: transparent;
  border-right-color: transparent;
  animation: animate 2s infinite;
}

#object_one {
  left: 75px;
  top: 75px;
  width: 50px;
  height: 50px;
}

#object_two {
  top: 65px;
  left: 65px;
  width: 70px;
  height: 70px;
  animation-delay: 0.2s;
}

#object_three {
  top: 55px;
  left: 55px;
  width: 90px;
  height: 90px;
  animation-delay: 0.4s;
}

#object_four {
  top: 45px;
  left: 45px;
  width: 110px;
  height: 110px;
  animation-delay: 0.6s;
}

@keyframes animate {
