  font-size: 15px;
  line-height: 1.2;
  letter-spacing: 0.05em;
}

.tooltip-inner {
  border-radius: 0;
}

.tooltip-light .tooltip-inner {
  padding: 15px 16px;
  border-radius: 4px;
  background: #ffffff;
  box-shadow: 0 7px 16px 0 rgba(0, 0, 0, 0.08);
  color: #2c343b;
}

.tooltip-light.bs-tooltip-top .arrow::before {
  border-top-color: #ffffff;
}

.tooltip-light.bs-tooltip-right .arrow::before {
  border-right-color: #ffffff;
}

.tooltip-light.bs-tooltip-bottom .arrow::before {
  border-bottom-color: #ffffff;
}

.tooltip-light.bs-tooltip-left .arrow::before {
  border-left-color: #ffffff;
}

.tooltip-point {
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 10px;
  height: 10px;
  background: #0accbe;
  box-shadow: 0 0 9.5px 0.5px rgba(30, 34, 44, 0.3);
  transition: opacity 0.5s ease-out, transform 0.25s;
  transform: scale(1);
  backface-visibility: hidden;
  cursor: pointer;
}

.tooltip-point,
.tooltip-point::before,
.tooltip-point::after {
  border-radius: 50%;
}

.tooltip-point::before,
.tooltip-point::after {
  content: "";
  position: absolute;
  top: -43px;
  left: -43px;
  z-index: -1;
  width: 100px;
  height: 100px;
  background: rgba(10, 204, 190, 0.4);
  pointer-events: none;
}

.tooltip-point::before {
  animation: preview-marker-pulsar 1.7s infinite ease-out;
}

.tooltip-point::after {
  animation: preview-marker-pulsar-inner 1.7s infinite ease-out;
}

.tooltip-point:hover::after,
.tooltip-point:hover::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: none;
}

@keyframes preview-marker-pulsar-inner {
  0% {
    opacity: 0;
    transform: scale(0.1);
  }
  24%,
  90% {
    opacity: 0;
  }
  25% {
    opacity: 0.65;
    transform: scale(0.1);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

@keyframes preview-marker-pulsar {
  0% {
    opacity: 0.65;
    transform: scale(0.1);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

