/*
* Material Parallax
*/
.parallax-content {
  position: relative;
  z-index: 1;
}

.parallax-container {
  position: relative;
  overflow: hidden;
  background-position: center center;
  background-size: cover;
}

@supports (clip-path: polygon(0 0)) or (-webkit-clip-path: polygon(0 0)) {
  .parallax-container {
    overflow: visible;
  }
  .material-parallax {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

.material-parallax {
  position: absolute;
  top: 0;
  left: -1px;
  right: -1px;
  bottom: 0;
  z-index: 0;
  background-position: center center;
}

.ipad .parallax-container,
.iphone .parallax-container {
  background-attachment: scroll !important;
}

.material-parallax {
  pointer-events: none;
}

.material-parallax img {
  display: none;
  position: absolute;
  left: 50%;
  bottom: 0;
  min-width: 101%;
  min-height: 101%;
  max-width: none;
  transform: translate3d(-50%, 0, 0);
}

[data-x-mode="true"] .material-parallax {
  pointer-events: auto;
}

.parallax-outer .parallax-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
}

